.result-container{font-family:'comfortaa', 'Roboto', sans-serif;background-color:#d9dad7;color:#1a2639;opacity:1;padding:10px;margin:10px;text-align:center;justify-content:center}.result-container .filter-button{border:none;background:inherit;padding:0.3rem 0.5rem;margin:0.3rem;outline:none}.result-container .filter-button:hover{background:#f4f4f4;cursor:pointer}.result-container #user-list-item{max-width:800px;display:grid;margin:0.5em;grid-template-columns:5fr repeat(2, 1fr);align-items:center}.result-container .product-list-item{max-width:800px;display:grid;margin:0.5em;grid-template-columns:6fr 1fr}.result-container button{border:none;background-color:#d9dad7;box-shadow:8px 8px 8px #828381;color:#1a2639;font-size:16px;margin:0.3rem;padding:0.5rem;border-radius:22px;box-shadow:8px 8px 12px #b4b5b2, -8px -8px 12px #ececec}.result-container button:hover{box-shadow:10px 10px 15px #b4b5b2, -10px -10px 15px #ececec;background:#c3c4c1;cursor:pointer}.result-container .button:active{box-shadow:4px 4px 8px #b4b5b2, -4px -4px 8px #ececec}.result-container .button:focus{outline:none}.result-container .users-search-list{display:grid;margin:1rem;text-align:center;justify-content:center;border-radius:30px;margin-top:2.5rem;box-shadow:9px 9px 20px #b8b9b7, -9px -9px 20px #fafbf7}.result-container .items-search-list{display:grid;margin:1rem;text-align:center;justify-content:center;border-radius:8px;margin-top:2.5rem;box-shadow:9px 9px 20px #b8b9b7, -9px -9px 20px #fafbf7}

.nav{width:6rem;height:100vh;margin:0;padding:3rem 0;position:fixed;z-index:1000;left:0;background-color:#3e4a61;display:flex;justify-content:space-around;align-items:center;flex-direction:column;box-shadow:3px 0px 10px 0.3px rgba(0,0,0,0.05);-webkit-transition:width 200ms ease;transition:width 200ms ease;background:#3e4a61;box-shadow:11px 11px 22px #888888, -11px -11px 22px #ffffff}.nav ul{list-style:none;width:100%;margin:0;padding:0;display:flex;flex-direction:column;text-align:center;list-style:none}.nav ul li{color:#d9dad7;margin:10px;padding:0.7rem;-webkit-transition:background-color 50ms ease;transition:background-color 50ms ease;border-radius:40px;background:#3e4a61;box-shadow:5px 5px 10px #2d3546, -5px -5px 10px #4f5f7c}.fa-home,.fa-shopping-cart,.fa-comments,.fa-user-circle,.fa-sign-out-alt{color:#d9dad7;padding:0}.nav-text{display:none;color:#d9dad7;text-shadow:2px 2px 4px #262d3b, -2px -2px 4px #566787;-webkit-animation:0.7s fadeIn;animation:0.7s fadeIn}.nav:hover{width:16rem}.nav:hover .nav-text{display:inline-block}.nav:hover li:active{border-radius:40px;background:#3e4a61;box-shadow:2px 2px 5px #2d3546, -2px -2px 5px #4f5f7c}.nav:hover li:hover{background:#3e4a61}.nav:hover li:hover i{color:#fff;text-shadow:5px 5px 5px 5px #d9dad7}.nav:hover li:hover span{color:#fff}.nav:hover .nav-item{display:grid;grid-template-columns:1fr 1fr;justify-content:space-between;align-items:center}.nav:hover .nav-item span{text-align:left}.nav-item{display:grid;justify-content:center;align-items:center;margin:0.5rem}@-webkit-keyframes fadeIn{0%{opacity:0}1%{display:inline-block}100%{opacity:1}}@keyframes fadeIn{0%{opacity:0}1%{display:inline-block}100%{opacity:1}}.app-logo{border-radius:40px;background:#3e4a61;box-shadow:5px 5px 10px #2d3546, -5px -5px 10px #4f5f7c}@media only screen and (max-width: 600px){.nav{position:-webkit-sticky;position:sticky;top:0;left:0;height:3rem;width:100vw;flex-direction:row}.nav ul{display:flex;flex-direction:row;justify-content:center}.nav .app-list-item{display:none}.nav:hover{width:100vw;justify-content:left}.nav:hover .nav-item{display:flex}.nav:hover .nav-text{display:none}}

.searchbar{width:80%;margin:auto;display:flex;justify-content:center;border:none;padding:0.2rem 0.5rem;border-bottom:1px solid rgba(0,0,0,0.9);outline:none;background:inherit}.searchbar::-webkit-input-placeholder{color:rgba(0,0,0,0.9)}.searchbar:-ms-input-placeholder{color:rgba(0,0,0,0.9)}.searchbar::-ms-input-placeholder{color:rgba(0,0,0,0.9)}.searchbar::placeholder{color:rgba(0,0,0,0.9)}.search-container{padding:1rem;margin-bottom:2rem;box-shadow:2px 1px 10px 1px rgba(0,0,0,0.1)}

